import React from "react";
import jsonData from "../../json/data.json";
import TransitionLink from "../../hooks/TransitionLink";
const Work = () => {
  return (
    <div className="w-full flex flex-col items-center relative mt-3">
      <p className="text-4xl text-5xls z-10 mb-3">
        <span className="flex items-center flex-col relative">
          <span className="text-3xl sm:text-4xl text-gradB ">
            Work
          </span>
          <span className="text-5xl text-gradB Alfa text-center">History</span>
        </span>
      </p>
      <div className=" inline-flex max-w-7xl">
        <ul className="flex gap-2 p-4 items-center flex-col">
          {jsonData.work_history.map(({ company, img,link }, index) => (
            <li className={``} key={index}><TransitionLink target='blank' href={link}>
              <p className="gradient_purple_text text-md hover:scale-110 transition-all">{company}</p>
              {/* <img
                className="max-h-[72px] max-w-[144px] box-border transition-all cursor-pointer  hover:contrast-200 hover:scale-150"
                src={img}
                alt={company}
                title={company}
              /> */}
              </TransitionLink>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default Work;
